@font-face {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    src: local('DM Sans Regular'), url('../fonts/DMSans-Regular.ttf') format('ttf');
  }

  @font-face {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    src: local('DM Sans Italic'), url('../fonts/DMSans-Italic.ttf') format('ttf');
